import { useEffect, useState } from "react";

const City = () => {
  const loadWidget = async () => {
    const data = await fetch("http://localhost:5000/widgets");
    const widgets = await data.json();
  };

  const [deleteWidget, setDeleteWidget] = useState("");

  console.log(deleteWidget);

  useEffect(() => {
    loadWidget();
  }, []);

  return (
    <div className="flex mt-20 mr-2 flex-wrap px-10">
      {widgets.map((item) => (
        <div
          key={item._id}
          className="relative flex flex-col my-6 bg-white shadow-sm border border-slate-200 rounded-lg w-80 mr-10"
        >
          <div className="px-4 py-4">
            <div className="font-bold text-xl my-2 text-center ">
              {item.location}
            </div>
            <div className="text-gray-700 text-base">
              <p>
                <span className="font-bold text-lg">Current Temperature:</span>{" "}
                {(item?.weather?.main?.temp - 273).toFixed(0)}
              </p>
              <p>
                <span className="font-bold text-lg">Minimum Temperature:</span>{" "}
                {(item?.weather?.main?.temp_max - 273).toFixed(0)}
              </p>
              <p>
                <span className="font-bold text-lg">Maximum Temperature:</span>{" "}
                {(item?.weather?.main?.temp_min - 273).toFixed(0)}
              </p>
              <p>
                <span className="font-bold text-lg">Description:</span>{" "}
                {item?.weather?.weather?.description}
              </p>
              <p>
                <span className="font-bold text-lg">Feels like:</span>{" "}
                {(item?.weather?.main?.feels_like - 273).toFixed(0)}
              </p>
            </div>
            <button onClick={() => setDeleteWidget(item?.weather?._id)}>
              Delete
            </button>
          </div>
        </div>
      ))}
    </div>
  );
};

export default City;
